.AboutSection{background-color:#232023}.AboutSection .AboutContent{margin:6em 2em}.AboutSection .AboutColumns{display:flex;text-align:center;line-height:1.3;margin-top:4em;gap:1em}@media(width < 992px){.AboutSection .AboutColumns{flex-direction:column}}.AboutSection .AboutItem{flex:1}.AboutSection .AboutItem .Number{font-family:var(--softFontIt);color:var(--goldColor);font-size:6em;line-height:1}.AboutSection .AboutItem .Number small{font-size:60%}.AboutSection .AboutItem .Explanation{text-transform:uppercase;color:#fff;font-size:1.2em}.AboutSection .AboutItem .Text{color:var(--goldColor);font-size:1.2em;margin-top:1em}