.ServicesSection{background-color:#272a2f}.ServicesSection .ServicesContent{margin:4em 2em}.ServicesSection .ItemsRow{margin-top:4em;position:relative;display:flex;flex-direction:column;gap:2em}@media(width >= 992px){.ServicesSection .ItemsRow{flex-direction:row}}.ServicesSection .ItemsRow>li{flex:1;position:relative;display:flex;flex-direction:column;gap:1em;color:var(--goldColor);margin:0}.ServicesSection .ItemImg{user-select:none;position:relative;height:120px;border-radius:8px}.ServicesSection .ItemImg,.ServicesSection .ItemImg:after{background-position:center;background-repeat:no-repeat;background-size:cover}.ServicesSection .ItemImg#estimate,.ServicesSection .ItemImg#estimate:after{background-image:url("../images/Services/thumbs/estimate.jpg")}.ServicesSection .ItemImg#agreement,.ServicesSection .ItemImg#agreement:after{background-image:url("../images/Services/thumbs/agreement.jpg")}.ServicesSection .ItemImg#photo,.ServicesSection .ItemImg#photo:after{background-image:url("../images/Services/thumbs/photo.jpg")}.ServicesSection .ItemImg#preparation,.ServicesSection .ItemImg#preparation:after{background-image:url("../images/Services/thumbs/preparation.jpg")}.ServicesSection .ItemImg#support,.ServicesSection .ItemImg#support:after{background-image:url("../images/Services/thumbs/support.jpg")}.ServicesSection .ItemText{font-family:var(--softFont);font-size:1.5em;line-height:1.3;text-align:center}