.GoalsSection{background-color:#272a2f}.GoalsSection .GoalsContent{margin:6em 2em;position:relative;display:flex;flex-direction:column}.GoalsSection .ItemsRow{margin-top:4em;position:relative;display:flex;flex-direction:column;gap:2em}@media(width >= 992px){.GoalsSection .ItemsRow{flex-direction:row}}.GoalsSection .ItemsRow>li{flex:1;position:relative;display:flex;flex-direction:column;gap:1em;color:var(--goldColor);margin:0}.GoalsSection .Item{flex:1;display:flex;flex-direction:column;align-items:flex-start;gap:1.5em;line-height:1.3}.GoalsSection .Item p{font-size:1.5em;flex:1}