.HeroSection{background:radial-gradient(70% 60% at 80% 30%, rgba(var(--goldRGB)/30%) 0%, rgba(var(--goldRGB)/0%) 100%);display:flex;flex-direction:column}.HeroSection .HeroVisualBlock{position:relative;overflow:hidden;height:50vmin;min-height:200px}.HeroSection .HeroVisualBlock .FullVisual{user-select:none}.HeroSection .HeroVisualBlock .FullVisual,.HeroSection .HeroVisualBlock .FullVisual:after{position:absolute;top:0;bottom:0;left:0;right:0;background-image:url("../images/Hero/visual-background-thumb.jpg");background-position:center;background-repeat:no-repeat;background-size:cover}.HeroSection .HeroMainBlock{display:flex;justify-content:center;align-items:center}.HeroSection .HeroMainBlock .Wrapper{padding:20px;text-align:center}.HeroSection .HeroMainBlock .Title{color:var(--goldColor);font-size:6vw;font-family:var(--softFont);text-transform:uppercase}.HeroSection .HeroMainBlock .SubTitle{color:#fff;font-size:3vw;line-height:1.3}.HeroSection .HeroMainBlock .LandingButton{font-size:1em;padding:1em 2em;text-transform:uppercase;border-radius:6px;display:block;margin:2em auto;white-space:normal;transition:all var(--transitionTimeMs)}.HeroSection .HeroMainBlock .LandingButton:hover{opacity:.8}.HeroSection .AreaLogo{text-align:center;display:flex;margin:2.25em 0}.HeroSection .AreaLogo,.HeroSection .AreaLogo a{display:flex;flex-direction:column;align-items:center}.HeroSection .AreaLogo a{transition:all var(--transitionTimeMs)}.HeroSection .AreaLogo a .Logo{user-select:none;width:40vw;height:10vw;background-image:url("../images/Hero/area-logo.svg");background-position:center;background-repeat:no-repeat;background-size:contain}.HeroSection .AreaLogo a span{display:block;color:#fff}.HeroSection .AreaLogo a span.Text2{color:var(--goldColor)}.HeroSection .AreaLogo a:hover,.HeroSection .AreaLogo a:hover span{text-decoration:none;opacity:.8}@media(width >= 992px){.HeroSection .AreaLogo,.HeroSection .AreaLogo a{align-items:flex-start}.HeroSection .AreaLogo a .Logo{width:23vw;hei