.Spinner{display:inline-block;width:50px;height:50px;border:3px solid hsla(0,0%,100%,.3);border-radius:50%;border-top-color:#fff;animation:Spinner 1s ease-in-out infinite}@keyframes Spinner{to{transform:rotate(360deg)}}.SpinnerSplash{position:absolute;left:0;top:0;bottom:0;right:0;overflow:hidden;background-color:rgba(0,0,0,.8);display:flex;justify-content:center;align-items:center}